.overlay {
  z-index: 99;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .7);
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: opacity, visibility;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

.overlay.overlay-active {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}

.overlay.overlay-active .modal {
  transform: scale(1);
}

.close-button {
  color: #d41443;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-button:hover {
  transform: rotate(90deg);
}

.close-icon {
  width: 14px;
  height: 14px;
}

.modal {
  width: 480px;
  color: #d41443;
  background-color: #ffb8ca;
  border: 1px solid #646464;
  border-radius: 14px;
  padding: 40px 15px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  transform: scale(.8);
}

.modal__title {
  text-align: center;
  margin-bottom: 15px;
  font-family: Titan One, sans-serif;
  font-size: 24px;
  line-height: 1.4;
}

.modal__text {
  text-align: center;
  margin-bottom: 20px;
  font-family: Titan One, sans-serif;
  font-size: 14px;
  line-height: 1.4;
}

.modal__item {
  font-size: 12px;
  font-weight: 700;
  position: relative;
}

.modal__item:not(:last-child) {
  margin-bottom: 15px;
}

.modal__label {
  position: absolute;
  top: 12px;
  left: 10px;
}

.modal__input {
  width: 100%;
  color: inherit;
  background-color: rgba(255, 255, 255, .565);
  border: none;
  border-radius: 6px;
  padding: 38px 10px 16px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), outline .25s cubic-bezier(.4, 0, .2, 1);
}

.modal__input:hover {
  background-color: rgba(255, 255, 255, .44);
}

.modal__input:focus {
  outline: 2px solid #d41443;
}

.modal__button {
  color: #d41443;
  background-color: #fff;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 22px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 43px;
  font-size: 16px;
  font-weight: 700;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), outline .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.modal__button:hover {
  color: #fff;
  background-color: #d41443;
}

.modal__button:focus {
  outline: 2px solid #d41443;
}

.modal__symbol {
  margin-left: 10px;
}

.modal__image {
  display: none;
}

@media screen and (min-width: 768px) {
  .overlay {
    padding: 32px;
  }

  .modal {
    width: 600px;
    padding: 40px 80px;
  }

  .modal__title {
